<template>
  <div class="training">
    <myHeader :isBack="true" title="理财申请"></myHeader>
    <div class="seat-head"></div>
    <iframe :src="'http://mobile.blc1866.com/indexPage'" frameborder="0" class="frameStyle">

    </iframe>
  </div>
</template>

<script>
  import myHeader from '@/components/Header'

  export default {
    name: 'financialApply',
    data () {
      return {
        id: ''
      }
    },
    components: {
      myHeader
    },
    mounted() {
      let urlData = this.$route.fullPath;
      this.id=urlData.slice(urlData.indexOf('=')+1);
    },
    methods: {
    }
  }
</script>

<style scoped lang="scss">
  .training {
    min-height: 100vh;
    background-color: #F8F8F8;
    position: relative;
  }

  .frameStyle {
    width: 100%;
    height:calc(100% - 40px);
    overflow: hidden;
    top: 40px;
    left: 0;
    position: absolute;

  }
</style>



